import {SensorType} from "../../enum/SensorType";
import {LabType} from "../../enum/LabType";
import Lab from "../Lab";
import type {AccelerometerMode} from "../../enum/AccelerometerMode";
import type {RawSensorValues} from "../../dto/RawSensorValues";
import type {float} from "../../types/numbers";
import type {LabRepository} from "../../repository/LabRepository";
import type {ArchiveValues} from "../../dto/ArchiveValues";
import type {FactorySettings} from "../../dto/FactorySettings";
import type {UserSettings} from "../../dto/UserSettings";
import type {SensorArchiveValues} from "../../dto/SensorArchiveValues";
import {SensorValuesConverter} from "../../protocol/SensorValuesConverter";


export class PhysicsLab extends Lab
{
    public readonly availableSensors: ReadonlyArray<SensorType> = [
        SensorType.TempSensor,
        SensorType.AbsolutePressureSensor,
        SensorType.TeslameterSensor,
        SensorType.VoltmeterSensor,
        SensorType.AmpermetrSensor,
        SensorType.AccelerometerXSensor,
        SensorType.AccelerometerYSensor,
        SensorType.AccelerometerZSensor,
    ];
    protected get labType() { return LabType.Eco};

    private accelerometerMode: AccelerometerMode

    public constructor(repository: LabRepository) {
        super(repository);
    }

    private valuesTemp = new Map<SensorType, float>()

    override updateModes(rawValues: RawSensorValues) {
        this.accelerometerMode = rawValues.accelerometerMode
    }



    override setMode(sensorType: SensorType, modeIdx: number): void {
        // TODO: implement accelerometer mode
    }

    override getMode(sensorType: SensorType): number {
        switch (sensorType) {
            case (SensorType.AccelerometerXSensor):
            case (SensorType.AccelerometerYSensor):
            case (SensorType.AccelerometerZSensor):
                return this.accelerometerMode;
        }
    }

}