<script lang="ts">
    import Modal from "./Modal.svelte";
    import strings from "../resources/strings";

    // Props
    export let link;
    export let onClose = () => {}

    // Internal

    function copyToClipboard() {
        self.navigator.clipboard.writeText(link)
    }

    function openInNewTab() {
        self.open(link, '_blank', 'noopener')
    }
</script>

<Modal onClose={onClose}>
    <div class="modal-content">
        <span class="header">{strings.archiveSavedTitle}</span>
        <span class="text">{strings.archiveSavedDescription}</span>
        <div class="row justify-between align-center">
            <a href={link} target="_blank">{strings.link}</a>
            <div class="buttons">
                <button on:click={() => copyToClipboard()}>{strings.copy}</button>
                <button on:click={() => openInNewTab()}>{strings.open}</button>
                <button on:click={() => onClose()}>{strings.close}</button>
            </div>
        </div>
    </div>
</Modal>

<style lang="scss">
  @import "src/styles/constants";

  .modal-content {
    justify-content: space-between;
    display: flex;
