import type {SolidColor} from "lightweight-charts";
import type Lab from "./Lab";
import type {float} from "../types/numbers";
import type {SensorType} from "../enum/SensorType";
import type {SensorMode} from "../repository/sensors";
import {SENSORS_PREFERENCES} from "../repository/sensors";
import {loop_guard} from "svelte/internal";
import {chartColors} from "../repository/colors";
import {round} from "../utils/math";

export default class Sensor {
    get mode(): SensorMode {
        return this.modes[this.selectedModeIdx]
    }
    set mode(mode: SensorMode) {
        const idx = this.modes.indexOf(mode)
        if (idx === -1)
            return;

        if (this.modes.length > 0) {
            this.lab.setMode(this.sensorType, idx);
        }
        this.selectedModeIdx = idx;
    }
    private selectedModeIdx: number = 0;

    name: string;

    ico: string;

    accuracy: number;

    public readonly modes: SensorMode[]

    public markerColor: string = chartColors[0]

    public markerSize: number = 1;

    public lineColor: string = chartColors[0]

    public lineSize: 1 | 2 | 3 | 4 = 3;

    constructor(
        private readonly lab: Lab,
        public readonly sensorType: SensorType,
    ) {
        const preferences = SENSORS_PREFERENCES.get(sensorType)
        this.modes = preferences.modes
        this.name = preferences.name
        this.ico = preferences.ico
        this.accuracy = preferences.accuracy


        if (this.modes.length === 0)
            throw new Error("modes must contains a least one element")
    }

    getLastValue(): float | null {
        const value = this.lab.getValue(this.sensorType);
        if (isNaN(value) || value == null)
            return null;
        return round(value, this.accuracy)
    }
}