<script lang="ts">
    import Lab from "../api/model/Lab";
    import {Route, Router, useNavigate} from "svelte-navigator";
    import MainView from "../views/MainView.svelte";
    import {RoutePath} from "../routes";
    import type {LabRepository} from "../api/repository/LabRepository";
    import LabFactory from "../api/model/LabFactory";
    import {onMount} from "svelte";
    import strings from "../resources/strings";
    import DashboardScreen from "./DashboardScreen.svelte";

    // Props

    export let labRepository: LabRepository

    let lab: Lab

    onMount(async () => {
        try {
            lab = await LabFactory.create(labRepository)
            lab.onError.addEventListener((message) => {
                alert(message)
            })
        } catch (e) {
            console.debug(e)
            alert(strings.fatalError)
        }
        return () => {
            lab.stop()
        }
    })
    // Internal

    $: sensors = lab?.getSensors() ?? [];
    $: canGoToDashboard = sensors.length > 0;

    const navigate = useNavigate()

    lab?.onError.addEventListener(() => {
        canGoToDashboard = false
    })

</script>
<Router>
    <Route>
        <MainView {sensors} on:start={() => navigate(RoutePath.Dashboard)} {canGoToDashboard} />
    </Route>
    <Route path={RoutePath.Dashboard}>
        <DashboardScreen {lab} {labRepository}/>
    </Route>
</Router>
