import type {byte} from "../types/numbers";
import {BitConverter} from "../utils/BitConverter";
import {crc16} from "../utils/crc16";

export class Commands {
    static readonly userDataLength: number = 108;

    static readonly unsupportMsg: byte = 0xFF;
    static readonly startByte: byte = 0xAA;

    static readonly data_exchange: byte = 0x10;
    static readonly get_HW_Config: byte = 0x31;
    static readonly get_UserData: byte = 0x41;
    static readonly get_ArchiveBt: byte = 0x22;
    static readonly set_UserData: byte = 0x40;
    static readonly proto_msgType_getInfo: byte = 0x00;
    static readonly archive_Control: byte = 0x20;
    static readonly getArchive: byte = 0x21;
    static readonly proto_msgType_bootloader_get_FW_Cfg: byte = 0x83;


    /**
     * Get lab info command (mode, configs, type, etc.)
     * @constructor
     */
    public static getInfo(): ArrayBuffer {
        const writeMessage = new Uint8Array(6);
        writeMessage[0] = this.startByte;

        writeMessage[1] = BitConverter.GetBytes(writeMessage.byteLength)[0];
        writeMessage[2] = BitConverter.GetBytes(writeMessage.byteLength)[1];

        writeMessage[3] = this.proto_msgType_getInfo;

        const crc = this.calculateMessageCrc16(writeMessage);
        writeMessage[4] = crc[0];
        writeMessage[5] = crc[1];

        console.log(writeMessage)

        return writeMessage;
    }

    public static errorMessage(): ArrayBuffer {
        const writeMessage = new Uint8Array(6);
        writeMessage[0] = this.startByte;

        writeMessage[1] = BitConverter.GetBytes(writeMessage.byteLength)[0];
        writeMessage[2] = BitConverter.GetBytes(writeMessage.byteLength)[1];

        writeMessage[3] = this.unsupportMsg;

        const crc = this.calculateMessageCrc16(writeMessage);
        writeMessage[4] = crc[0];
        writeMessage[5] = crc[1];

        return writeMessage;
    }

    private static _readSensorValuesMessage: ArrayBuffer
    /**
     * Get read sensor values command
     * @constructor
     */
    public static getSensorValues(): ArrayBuffer {
        if (this._readSensorValuesMessage != undefined)
            return this._readSensorValuesMessage 
            
        const writeMessage = new Uint8Array(6);
        writeMessage[0] = this.startByte;
        writeMessage[1] = BitConverter.GetBytes(writeMessage.byteLength)[0];
        writeMessage[2] = BitConverter.GetBytes(writeMessage.byteLength)[1];
        writeMessage[3] = this.data_exchange;

        const crc = this.calculateMessageCrc16(writeMessage);
        writeMessage[4] = crc[0];
        writeMessage[5] = crc[1];

        this._readSensorValuesMessage = writeMessage;
        return this._readSensorValuesMessage;
    }

    /**
     * Get write scales (modes) of accelerometer and conductometer message 
     * @param accelScale
     * @param conductScale
     * @constructor
     */
    public static setScales(accelScale: byte, conductScale: byte): ArrayBuffer {
        const writeMessage = new Uint8Array(8);
        writeMessage[0] = this.startByte;
        writeMessage[1] = BitConverter.GetBytes(writeMessage.byteLength)[0];
        writeMessage[2] = BitConverter.GetBytes(writeMessage.byteLength)[1];
        writeMessage[3] = this.data_exchange;

        writeMessage[4] = accelScale;
        writeMessage[5] = conductScale;

        const crc = this.calculateMessageCrc16(writeMessage);
        writeMessage[6] = crc[0];
        writeMessage[7] = crc[1];

        return writeMessage;
    }

    /**
     * Get factory values message
     * @constructor
     */
    public static getFactorySettings(): ArrayBuffer {
        const writeMessage = new Uint8Array(6);
        writeMessage[0] = this.startByte;
        writeMessage[1] = BitConverter.GetBytes(writeMessage.byteLength)[0];
        writeMessage[2] = BitConverter.GetBytes(writeMessage.byteLength)[1];
        writeMessage[3] = this.get_HW_Config;

        const crc = this.calculateMessageCrc16(writeMessage);
        writeMessage[4] = crc[0];
        writeMessage[5] = crc[1];

        return writeMessage;
    }

    /**
     * Get user values message
     * @param dataOffset
     * @param dataLength
     * @constructor
     */
    public static getUserSettings(dataOffset: byte, dataLength: byte): Uint8Array {
        const writeMessage = new Uint8Array(8);
        writeMessage[0] = this.startByte;
        writeMessage[1] = BitConverter.GetBytes(writeMessage.byteLength)[0];
        writeMessage[2] = BitConverter.GetBytes(writeMessage.byteLength)[1];
        writeMessage[3] = this.get_UserData;

        writeMessage[4] = dataOffset;
        writeMessage[5] = dataLength;


        const crc = this.calculateMessageCrc16(writeMessage);
        writeMessage[6] = crc[0];
        writeMessage[7] = crc[1];

        return writeMessage;
    }

    

    private static calculateMessageCrc16(bytes: Uint8Array): Uint8Array {
        return crc16(bytes.slice(0, bytes.byteLength - 2));
    }

    public static checkAnswer(answerArray: Uint8Array): boolean {
        if (answerArray.byteLength < 6)
            return false;

        const byteBuffer = new Uint8Array(answerArray)
        const crc = this.calculateMessageCrc16(byteBuffer);
        return crc[0] == byteBuffer[byteBuffer.byteLength - 2] &&
            crc[1] == byteBuffer[byteBuffer.byteLength - 1];
    }
}