import type {float, short, uint, ushort} from "../types/numbers";

export class BitConverter {
    public static ToSingle(buffer: ArrayBuffer, index: number): float {
        return new DataView(buffer).getFloat32(index, true)
    }
    public static ToInt16(buffer: ArrayBuffer, index: number): short {
        return new DataView(buffer).getInt16(index, true)
    }
    public static ToUInt16(buffer: ArrayBuffer, index: number): ushort {
        return new DataView(buffer).getUint16(index, true)
    }
    public static ToUInt32(buffer: ArrayBuffer, index: number): uint {
        return new DataView(buffer).getUint32(index, true)
    }

    public static GetBytes(integer: number) {
        const buffer = new Uint8Array(4)
        for (let i = 0; i < 4; i++) {
            buffer[i] = (integer >> (i * 8) & 255)
        }
        return buffer
    }
}