<script lang="ts" context="module">
    import {TimeUnit} from "../enum/TimeUnit";

    let _timeUnit = TimeUnit.Second
</script>

<script lang="ts">
    import ContentHeader from "../components/ContentHeader.svelte";
    import strings from "../resources/strings";
    import {TimeFormat} from "../enum/TimeFormat";
    import {Settings} from "../Settings";
    import Title from "../components/Title.svelte";

    const maxTime = 60

    let timeUnit = _timeUnit;
    let timeValue = Settings.experimentTime / timeUnit

    $: {
        if (timeValue > maxTime)
            timeValue = maxTime
        Settings.experimentTime = timeValue * timeUnit
        _timeUnit = timeUnit;
    }

</script>

<div class="content-wrapper">
    <ContentHeader title="{strings.settings}" iconUri="/img/Gear.png"/>
    <Title title="{strings.experimentSettings}"/>

    <div class="settings">
        <div class="setting-row">
            <label>{strings.experimentTimeLabel}</label>
            <div class="setting-value">
                <input type="number" min="0" max={maxTime} bind:value={timeValue}>
                <select bind:value={timeUnit}>
                    <option value={TimeUnit.Second}>{strings.timeSecond}</option>
                    <option value={TimeUnit.Minute}>{strings.timeMinute}</option>
                </select>
            </div>
        </div>
        <div class="setting-row">
            <label>{strings.timeFormatLabel}</label>
            <select class="setting-value">
                <option value={TimeFormat.Stopwatch}>{strings.modeStopwatch}</option>
                <option value={TimeFormat.MinutesSeconds}>{strings.modeMinutesAndSeconds}</option>
                <option value={TimeFormat.HoursMinutes}>{strings.modeHoursAndMinutes}</option>
            </select>
        </div>
    </div>
</div>

<style lang="scss">
  @import "src/styles/colors";
  @import "src/styles/constants";
  @import "src/styles/global";
  @import "src/styles/mixins";

  .content-wrapper {
    @extend .full;
    @extend .column;
    padding: $settings-padding;
    overflow-y: auto;
  }

  @media screen and (max-width: 768px) {
    .content-wrapper {
      padding: $settings-padding-sm;
    }
  }

</style>
