import type {float} from "../types/numbers";

type Coefficients = {
    factoryK?: float,
    factoryD?: float,
    userK?: float,
    userD?: float
}

const defaultCoefficients = {
    factoryK: 1,
    factoryD: 0,
    userK: 1,
    userD: 0,
} as Coefficients

export function applyCoefficients(value: number, coefficients:Coefficients): float {
    const coefs = {...defaultCoefficients, ...coefficients}
    const factory = value * coefs.factoryK + coefs.factoryD;
    return factory * coefs.userK + coefs.userD;
}

export function round(value: number, accuracy: number) {
    const multiplier =  Math.pow(10,accuracy)
    return Math.floor(value * multiplier) / multiplier
}