<script lang="ts">
    import Logo from "../components/Logo.svelte";
    import strings from "../resources/strings";
    import {SENSORS_PREFERENCES} from "../api/repository/sensors";
    import AboutView from "./AboutView.svelte";
    import SensorSettingsView from "./SensorSettingsView.svelte";
    import Sensor from "../api/model/Sensor";
    import {SensorType} from "../api/enum/SensorType";
    import {createEventDispatcher, onMount} from "svelte";
    import SettingsView from "./SettingsView.svelte";
    import WifiSettingsView from "./WifiSettingsView.svelte";
    import Hamburger from "../components/Hamburger.svelte";


    // Props

    export let sensors: Sensor[] = [];
    export let canGoToDashboard = false;

    // Inner

    const dispatch = createEventDispatcher()

    let selectedSensorIdx = -1
    let settingsSelected = false;
    let wifiSettingsSelected = false;
    let aboutSelected = false;
    let sensorsSelected = false;
    let navExpanded = self.innerWidth > 800

    function resetSelection() {
        selectedSensorIdx = -1;
        settingsSelected = false;
        aboutSelected = false;
        sensorsSelected = false;
        wifiSettingsSelected = false;
    }

    onMount(() => {
        self.addEventListener("resize", () => {
            navExpanded = self.innerWidth > 800
        })
    })


</script>

<div class="row full">
    <nav class="column" class:hidden={!navExpanded}>
        <div class:burger={navExpanded}>
            <Hamburger bind:value={navExpanded} />
        </div>

        {#if navExpanded}
            <Logo />
        {/if}
        <div class="menu-item large" class:selected={sensorsSelected}
             on:click={() => {resetSelection(); sensorsSelected = true}}
        >
            <img src="/img/Compass.png">
            <span>{strings.sensors}</span>
        </div>
        <div class="column sensors-list">
            {#each sensors as sensor, i (sensor.name)}
                <div class="menu-item" class:selected={selectedSensorIdx == i}
                     on:click={() => {resetSelection(); selectedSensorIdx = i}}
                >
                    <img src={sensor.ico}>
                    <span>{sensor.name}</span>
                </div>
            {/each}
        </div>
        <div class="settings-menu">

            <div class="menu-item large" class:selected={settingsSelected}
                 on:click={() => {resetSelection(); settingsSelected = true}}
            >
                <img src="/img/Gear.png">
                <span>{strings.experimentSettings}</span>
            </div>

            <div class="menu-item" class:selected={wifiSettingsSelected}
                 on:click={() => {resetSelection(); wifiSettingsSelected = true}}
            >
                <img src="/img/Gear.png">
                <span>{strings.wifiSettings}</span>
            </div>

            <div class="menu-item" class:selected={aboutSelected}
                 on:click={() => {resetSelection(); aboutSelected = true}}
            >
                <img src="/img/Info.png">
                <span>{strings.about}</span>
            </div>

        </div>
        <button class="start-button" disabled={!canGoToDashboard} on:click={() => dispatch('start')}>
            <span>{strings.start}</span>
            <img src="/img/ico_start.png"/>
        </button>
    </nav>

    <main>
        {#if sensorsSelected}
        {:else if selectedSensorIdx !== -1}
            <SensorSettingsView sensor={sensors[selectedSensorIdx]}/>
        {:else if settingsSelected}
            <SettingsView/>
        {:else if wifiSettingsSelected}
            <WifiSettingsView/>
        {:else if aboutSelected}
            <AboutView/>
        {/if}
    </main>
</div>

