﻿<script lang="ts">
    import strings from "../resources/strings";
    import {createEventDispatcher} from "svelte";
    import Class from "svelte/types/compiler/compile/nodes/Class";

    export let expanded: boolean = false
    export let selected: boolean = false

    export let value: number = 0;
    export let unit: string = "у.е.";
    export let minValue: number = 0;
    export let maxValue: number = 0;
    export let icon = "/sensors/Amper.png"
    export let label = "Датчик"

    const min = minValue;
    const max = maxValue;

    const eventDispatcher = createEventDispatcher()
    function emitApplyMinMax() {
        if (minValue < min)
            minValue = min
        if (maxValue > max)
            maxValue = max;
        if (minValue > maxValue)
            minValue = maxValue
        eventDispatcher('applyMinMax', { min: minValue, max: maxValue})
    }

</script>


<div class="content-wrapper" on:click class:selected={selected}>
    <img src="{icon}" class="icon">
    <label class="sensor-label">{label}</label>

    <div class="expander" class:expander_open={expanded}>

        <div class="expander-header">
            <span class="value">{value}</span>
            <span class="unit">{unit}</span>
            <button class="expander-button" on:click|stopPropagation={() => {expanded = !expanded}} ></button>
        </div>

        <hr class="expander-hr">

        <form on:submit|preventDefault={emitApplyMinMax} class="expander-body">
            <div class="row min-max">
                <label>{strings.maxY}</label>
                <input bind:value={maxValue} type="number" min={minValue} max={max}>
            </div>
            <div class="row min-max">
                <label>{strings.maxX}</label>
                <input bind:value={minValue} type="number" min={min} max={maxValue}>
            </div>
            <button class="apply-button" on:click={emitApplyMinMax}>
                {strings.apply}
            </button>
        </form>
    </div>
</div>

