<script context="module" lang="ts">
    export type ISensorTable = {
        addRow(newRow: number[]): void
        start(): void
        pause(): void
        getRowsSnapshot(): number[][]
    }
</script>

<script lang="ts">
    // Props

    import strings from "../resources/strings";
    import {msToString} from "../api/utils/dateTime";

    export let rows: number[][] = []
    export let headers: string[] = []
    export let isVisible: boolean = true

    // Internal
    let tempRows: number[][] = []
    let paused = false
    let rowsSnapshot: number[][] = []

    // External

    export function addRow(newRow: number[]) {
        rows = [newRow, ...rows]
    }

    export function pause() {
        rowsSnapshot = rows.slice()
        paused = true
    }

    export function start() {
        rows = [...tempRows.reverse(), ...rows]
        rowsSnapshot = []
        paused = false
    }

    export function getRowsSnapshot() {
        return paused ? rowsSnapshot : rows.slice();
    }
</script>


<div class="table-wrapper" class:hidden={!isVisible}>
    <div class="scroll">

        <table>
            <thead>
            <tr>
                {#each headers as header}
                    <td>{header}</td>
                {/each}
            </tr>
            </thead>
            <tbody>
            {#each (paused ? rowsSnapshot : rows) as row (row[0])}
                <tr>
                    {#each row as cell, colIdx}
                        {#if (cell == null)}
                            <td>{strings.noData}</td>
                        {:else if (colIdx === 0)}
                            <td>{msToString(cell)}</td>
                        {:else }
                            <td>{cell}</td>
                        {/if}
                    {/each}
                </tr>
            {/each}
            </tbody>
        </table>

    </div>
</div>
