import { LabType } from "../enum/LabType";
import type {LabRepository} from "../repository/LabRepository";
import {BioLab} from "./labs/BioLab";
import {EcoLab} from "./labs/EcoLab";
import {ChemLab} from "./labs/ChemLab";
import {PhysicsLab} from "./labs/PhysicsLab";

export default class LabFactory
{
    public static async create(labRepository: LabRepository)
    {
        try {
            const labType =  await labRepository.getLabType()
            switch (labType)
            {
                case (LabType.Phys):
                    return await new PhysicsLab(labRepository).init();
                case (LabType.Chem):
                    return await new ChemLab(labRepository).init();
                case (LabType.Eco):
                    return await new EcoLab(labRepository).init();
                case (LabType.Bio):
                    return await new BioLab(labRepository).init();
                default: throw new RangeError()
            }
        } catch (e) {
            return Promise.reject(e)

        }

    }
}