export function rowsToColumns<T>(rows: T[][]): T[][] {
    const colCount = rows[0].length;
    const result = Array<Array<T>>(colCount)
    for (let colIdx = 0; colIdx < colCount; colIdx++) {
        result[colIdx] = rows.map(r => r[colIdx])
    }

    return result
}

export function columnsToRows<T>(columns: T[][]): T[][] {
    const rowCount = columns[0].length;
    const result = Array<Array<T>>(rowCount)
    for (let rowIdx = 0; rowIdx < rowCount; rowIdx++) {
        result[rowIdx] = columns.map(r => r[rowIdx])
    }

    return result
}