<script lang="ts">
    import SavedDataView from "../views/SavedDataView.svelte";
    import type {LabRepository} from "../api/repository/LabRepository";
    import type {ArchiveValues} from "../api/dto/ArchiveValues";
    import {SensorType} from "../api/enum/SensorType";
    import {useEffect} from "../extensions/useEffect";


    // Props

    export let repository: LabRepository
    export let id: string

    // Internal

    let archiveValues

    useEffect(() => {
        if (!repository || !id)
            return

        repository.getArchiveValues(id).then((result) => {
            archiveValues = result;
        })
    }, () => [repository])

</script>

{#if (archiveValues)}
    <SavedDataView {archiveValues}/>
{/if}