<script lang="ts">
    import strings from "../resources/strings";
    import DashboardView from "../views/DashboardView.svelte";
    import IDashboardView from "../views/DashboardView.svelte";
    import Lab from "../api/model/Lab";
    import {useNavigate} from "svelte-navigator";
    import {Settings} from "../Settings";
    import type {LabRepository} from "../api/repository/LabRepository";
    import type {ArchiveValues} from "../api/dto/ArchiveValues";
    import {useEffect} from "../extensions/useEffect";
    import {RoutePath} from "../routes";
    import {apiBase} from "../configuration";
    import ModalOpenSavedArchive from "../components/ModalOpenSavedArchive.svelte";


    // Props

    export let lab: Lab
    export let labRepository: LabRepository

    // Internal
    let dashboard: IDashboardView
    $: sensors = lab?.getSensors() ?? [];
    const navigate = useNavigate()
    let showSavedWindow = false;
    let viewLink = undefined;

    useEffect(() => {
        if (!lab || !dashboard)
            return;
        lab.start()
        dashboard.start()

        lab.onError.addEventListener((message) => {
            dashboard.stop()
        })
        return () => {
            lab.stop()
            dashboard.stop()
            console.debug('dashboard stopped')
        }
    }, () => [lab, dashboard])


    function goBack() {
        navigate(-1)
    }

    function saveData(data: ArchiveValues) {
        console.log(data)
        labRepository.saveArchiveValues(data).then((id) => {
            if (!id) {
                alert(strings.loadFailed)
                return;
            }
            viewLink = `${window.location.origin}${RoutePath.SavedData}/${id}`
            showSavedWindow = true;
        })

    }

</script>

{#if (showSavedWindow)}
    <ModalOpenSavedArchive link={viewLink} onClose={() => showSavedWindow = false}/>
{/if}
<DashboardView {sensors} experimentTimeSeconds={Settings.experimentTime} saveData={saveData}
               intervalMs={250} bind:this={dashboard} on:back={() => goBack()}
/>