<script context="module" lang="ts">
    import {SeriesDataItemTypeMap, UTCTimestamp} from "lightweight-charts";

    export type ChartData = { time: UTCTimestamp | string | number, value: number };
    export type IChart = {
        addData: (newValue: ChartData) => void,
        zoomIn: () => void
        zoomOut: () => void
        resetZoom: () => void
        pause: () => void
        start: () => void
        applyMinMax: (min: number, max: number) => void
    }
</script>


<script lang="ts">
    import {
        createChart,
        IChartApi,
        ISeriesApi,
        ITimeScaleApi,
        Logical, SeriesDataItemTypeMap, SeriesMarker, Time, UTCTimestamp
    } from "lightweight-charts";
    import {onMount} from "svelte";
    import {msToString} from "../api/utils/dateTime";
    import strings from "../resources/strings";
    import Sensor from "../api/model/Sensor";


    // Props

    export let isVisible: boolean = true
    export let numberOfVisiblePoints = 100
    export let sensor: Sensor
    if (sensor === undefined)
        throw Error('SensorChart: Sensor must be specified')

    // Internal

    let minValue = sensor.mode.minValue
    let maxValue = sensor.mode.maxValue

    const labelX = strings.time
    const unit = sensor.mode.unit
    const labelY = `${sensor.name} [${sensor.mode.unit}]`
    const lineColor = sensor.lineColor
    const markerColor = sensor.markerColor
    const lineSize = sensor.lineSize
    const markerSize = sensor.markerSize

    type ChartData = SeriesDataItemTypeMap["Line"];

    const data = [] as ChartData[]

    let chartElement: HTMLDivElement;
    let timeScale: ITimeScaleApi = undefined;
    let chart: IChartApi
    let lineSeries: ISeriesApi<"Line">
    let isPaused: boolean = false

    let crosshairCurrentValue: string = "";
    let crosshairCurrentTime: string = "";
    let crosshairLabelOffsetX = 0
    let crosshairLabelOffsetY = 0
    let crosshairLabelVisible = false
    $: crosshairLabelRight = crosshairLabelOffsetX < chartElement?.clientWidth / 2
    $: crosshairLeftOffset = crosshairLabelRight ? crosshairLabelOffsetX + 'px' : ''
    $: crosshairRightOffset = crosshairLabelRight ? '' : (chartElement?.clientWidth - crosshairLabelOffsetX) + "px"

    onMount(() => {
        chart = createChart(chartElement, {
            crosshair: {
                vertLine: {
                    labelVisible: false
                }
            },
            rightPriceScale: {
                scaleMargins: {
                    top: 0.01,
                    bottom: 0.01
                },
            },
        })


        timeScale = chart.timeScale();
        timeScale.applyOptions({
            timeVisible: true,
            secondsVisible: true,
            tickMarkFormatter: msToString,
        })
        timeScale.setVisibleLogicalRange({from: -(numberOfVisiblePoints / 2), to: (numberOfVisiblePoints / 2)})

        lineSeries = chart.addLineSeries({
            lastValueVisible: true,
            color: lineColor,
            lineWidth: lineSize,
        });

        chart.subscribeCrosshairMove((event) => {
            crosshairLabelVisible = event.time && event.seriesPrices.size > 0;
            if (!crosshairLabelVisible) return;


            const value = event.seriesPrices.get(lineSeries)
            crosshairCurrentValue = value + unit
            crosshairCurrentTime = msToString(event.time as number)
            crosshairLabelOffsetX = event.point.x
            crosshairLabelOffsetY = event.point.y
        })

        update()

        const onResize = () => {
            chart.resize(chartElement.clientWidth, chartElement.clientHeight, true)
        }
        window.addEventListener('resize', onResize)

        applyMinMax(minValue, maxValue)

        return () => {
            chart.remove()
            window.removeEventListener('resize', onResize)
        }
    })

    const defaultMarker = {
        time: 0,
        position: 'inBar',
        color: markerColor,
        shape: 'circle',
        size: markerSize
    } as SeriesMarker<Time>

    function update() {
        lineSeries?.setData(data)
        lineSeries?.setMarkers(data.map(d => getMarker(d.time)))
    }
    function getMarker(time: Time):  SeriesMarker<Time> {
        const copy = Object.assign({},defaultMarker)
        copy.time = time
        return copy
    }


    // External

    // Subscribe to visible changed
    $: isVisible && !isPaused && update();

    export function addData(newValue: ChartData) {
        data.push(newValue)
        if (!isPaused && isVisible) // optimization
            update()
    }

    export function zoomIn() {
        const range = timeScale.getVisibleLogicalRange()
        const step = range.to / 10 as Logical;
        range.to -= step;
        range.from += step;
        if (range.to < range.from)
            return;
        timeScale.setVisibleLogicalRange(range)

    }

    export function zoomOut() {
        const range = timeScale.getVisibleLogicalRange()
        const step = range.to / 10 as Logical;
        range.to += step;
        range.from -= step;
        timeScale.setVisibleLogicalRange(range)
    }

    export function resetZoom() {
        const pivot = data.length
        timeScale.setVisibleLogicalRange({
            from: pivot - (numberOfVisiblePoints / 2),
            to: pivot + (numberOfVisiblePoints / 2)
        } as any)
    }

    export function pause() {
        isPaused = true;
        update()
    }

    export function start() {
        isPaused = false;
        update()
    }

    export function applyMinMax(min: number, max: number) {
        lineSeries.applyOptions({
            autoscaleInfoProvider: () => ({
                priceRange: {
                    minValue: min,
                    maxValue: max,
                },
                margins: {
                    above: 0.1,
                    below: 0.1,
                },
            }),
        })
    }


</script>
<div class="chart-wrapper" class:hidden={!isVisible}>
    <div bind:this={chartElement} class="chart">
        <div class="crosshair-label" hidden={!crosshairLabelVisible} style:top="{crosshairLabelOffsetY}px"
             style:left={crosshairLeftOffset} style:right={crosshairRightOffset}>
            <span>{strings.chartCrosshairValue} {crosshairCurrentValue}</span> <br>
            <span>{strings.chartCrosshairTime} {crosshairCurrentTime} [{strings.timeFormat}]</span>
        </div>
    </div>
    <div class="chart-label chart-label-y">{labelY}</div>
    <div class="chart-label chart-label-x">{labelX}</div>
</div>
