﻿<script lang="ts" context="module">
    import Sensor from "../api/model/Sensor";

    export type IDashboardView = {
        pause: () => void
        start: () => void
        stop: () => void
        sensors: Sensor[]
    }
</script>

<script lang="ts">
    import strings from "../resources/strings";
    import SidebarSensor from "../components/SidebarSensor.svelte";
    import {createEventDispatcher, onMount} from "svelte";
    import type IChart from '../components/SensorChart.svelte'
    import Sensor from "../api/model/Sensor";
    import SensorsTable, {getSnapshot} from "../components/DataTable.svelte";
    import type ISensorTable from "../components/DataTable.svelte";
    import {msToString} from "../api/utils/dateTime";
    import SensorChart from "../components/SensorChart.svelte";
    import type {float} from "../api/types/numbers";
    import BackButton from "../components/BackButton.svelte";
    import {DataDisplay} from "../enum/DataDisplay";
    import Logo from "../components/Logo.svelte";
    import {CustomEvent} from '../enum/CustomEvent'
    // noinspection TypeScriptCheckImport
    import {getModal} from "../components/Modal.svelte";
    // noinspection TypeScriptCheckImport
    import ModalDashboardConfirm, {id as modalId } from "../components/ModalDashboardConfirm.svelte";
    import type {ArchiveValues} from "../api/dto/ArchiveValues";
    import {rowsToColumns} from "../api/utils/array";
    import ModalOpenSavedArchive from "../components/ModalOpenSavedArchive.svelte";

    // Props

    export let sensors: Sensor[] = []
    export let intervalMs = 250;
    export let experimentTimeSeconds = 60;
    export let saveData: (data: ArchiveValues) => void = undefined;

    // Internal
    $: numberOfVisiblePoints = 1000 / intervalMs * experimentTimeSeconds
    const dispatch = createEventDispatcher();

    let dataDisplay: DataDisplay = DataDisplay.Measures
    let selectedIdx = 0;
    let paused = true;
    let stopped = false;

    let charts: IChart[] = [];
    let table: ISensorTable | undefined = undefined
    $: tableHeader = [`${strings.time} [${strings.timeFormat}]`, ...sensors.map(s => `${s.name} [${s.mode.unit}]`)];
    const values: float[] = [];
    let tableRows:  number[][] = []
    let showExitConfirm = false;

    onMount(() => {
        let startTime = Date.now();
        const timeout = setTimeout(() => {
            pause()
            alert(strings.experimentIsDone)
        }, experimentTimeSeconds * 1000)

        self.onbeforeunload = () => {
            return strings.exitFromDashboardTitle
        }

        setPause(true)
        const interval = setInterval(() => {
            const time = Date.now() - startTime;

            for (let i = 0; i < sensors.length; i++) {
                const newValue = sensors[i].getLastValue()

                values[i] = newValue;
                charts[i].addData({time: time, value: newValue})
            }

            tableRows = [[time, ...values], ...tableRows]
        }, intervalMs, true)
        return () => {
            clearInterval(interval);
            clearTimeout(timeout)
            self.onbeforeunload = null;
        }
    })

    function setPause(isPaused: boolean) {
        if (stopped && !isPaused)
            return;
        paused = isPaused;
        if (paused) {
            charts.forEach(c => c.pause())
            table.pause()
        } else {
            charts.forEach(c => c.start())
            table.start()
        }
    }

    function toggleMeasureGraphics() {
        dataDisplay = dataDisplay === DataDisplay.Measures ? DataDisplay.Graphics : DataDisplay.Measures
    }

    function selectSensor(idx: number) {
        selectedIdx = idx;
        dataDisplay = DataDisplay.Graphics;
    }

    function sentToCloud() {
        const rows = table.getRowsSnapshot()
        const columns = rowsToColumns(rows).map(col => col.reverse());
        const data: ArchiveValues = {
            time: columns[0],
            sensorsArchiveValues: sensors.map((s, i) => ({
                unit: s.mode.unit,
                sensorType: s.sensorType,
                values: columns[i + 1]
            }))
        }

        saveData(data)
    }

    function goBack() {
        showExitConfirm = true
    }

    // External

    export const pause = () => setPause(true)
    export const start = () => setPause(false)

    export function stop() {
        setPause(true)
        stopped = true
    }

</script>

<main class="full row">
    {#if (showExitConfirm)}
        <ModalDashboardConfirm onClose={(sureExit) => sureExit && dispatch(CustomEvent.Back) || (showExitConfirm = false)}/>
    {/if}
    <div class="column sidebar">
        <Logo/>
        <div class="sidebar-sensor-list">
            {#each sensors as sensor, i}
                <SidebarSensor value={values[i] ?? strings.noData} selected={selectedIdx === i}
                               maxValue={sensor.mode.maxValue} minValue={sensor.mode.minValue}
                               label={sensor.name} icon={sensor.ico} unit={sensor.mode.unit}
                               on:click={() => selectSensor(i)}
                               on:applyMinMax={({detail: {min, max}}) => charts[i].applyMinMax(min, max)}/>
            {/each}
        </div>
    </div>


    <div class="column work-screen">

        <div class="header">
            <div class="header__back-button">
                <BackButton on:click={goBack}/>

            </div>

            <h1 class="header__title">
                {dataDisplay === DataDisplay.Measures ? strings.measuresTitle : strings.graphicsTitle}
            </h1>

            <div class="row control-buttons">
                <button disabled={!saveData} on:click={() => sentToCloud()}>
                    <img src={"/img/ico_download.png"}>
                </button>
                <button class="play-pause" disabled={stopped} on:click={() => setPause(!paused)}>
                    <img src={paused ? "/img/ico_start.png" : "/img/ico_pause.png"}>
                </button>
                <button class="measure-graphics" on:click={toggleMeasureGraphics}>
                    <img src={dataDisplay === DataDisplay.Measures ? "/img/ico_measure.png" : "/img/ico_graphics.png"}>
                </button>
                <button disabled={dataDisplay === DataDisplay.Measures} on:click={charts[selectedIdx].resetZoom}>
                    <img src="/img/ico_zoom.png" alt="zoom">
                </button>
                <button disabled={dataDisplay === DataDisplay.Measures} on:click={charts[selectedIdx].zoomOut}>
                    <img src="/img/ico_zoom_out.png" alt="zoom_out">
                </button>
                <button disabled={dataDisplay === DataDisplay.Measures} on:click={charts[selectedIdx].zoomIn}>
                    <img src="/img/ico_zoom_in.png" alt="zoom_in">
                </button>
            </div>
        </div>

        <div class="charts-wrapper">
            {#each sensors as sensor, i}
                <SensorChart bind:this={charts[i]} isVisible={i === selectedIdx && dataDisplay === DataDisplay.Graphics}
                             {sensor} {numberOfVisiblePoints}/>
            {/each}
            <SensorsTable bind:this={table} bind:rows={tableRows} headers={tableHeader}/>
        </div>
    </div>

</main>

