<script lang="ts">
    import {onMount} from "svelte";
    import {createChart, IChartApi, ISeriesApi, ITimeScaleApi, Logical, SeriesMarker, Time} from "lightweight-charts";
    import {msToString} from "../api/utils/dateTime";
    import {ArchiveValues} from "../api/dto/ArchiveValues";
    import strings from "../resources/strings";
    import {SENSORS_PREFERENCES} from "../api/repository/sensors";
    import {round} from "../api/utils/math";
    import {chartColors} from "../api/repository/colors";

    // Props

    export let archiveValues: ArchiveValues
    export let isVisible: boolean = true

    // Internal

    let chartElement: HTMLDivElement;
    let timeScale: ITimeScaleApi = undefined;
    let chart: IChartApi
    let lineSeriesList: ISeriesApi<"Line">[] = []

    const labelX = strings.time

    let crosshairCurrentValues: string = "";
    let crosshairCurrentTime: string = "";
    let crosshairLabelOffsetX = 0
    let crosshairLabelOffsetY = 0
    let crosshairLabelVisible = false
    let crosshairValues: (number | undefined)[] = []
    $: crosshairLabelRight = crosshairLabelOffsetX < chartElement?.clientWidth / 2
    $: crosshairLeftOffset = crosshairLabelRight ? crosshairLabelOffsetX + 'px' : ''
    $: crosshairRightOffset = crosshairLabelRight ? '' : (chartElement?.clientWidth - crosshairLabelOffsetX) + "px"

    $: numberOfVisiblePoints = archiveValues.time.length

    onMount(() => {
        chart = createChart(chartElement, {
            crosshair: {
                vertLine: {
                    labelVisible: false
                }
            },
            rightPriceScale: {
                scaleMargins: {
                    top: 0.01,
                    bottom: 0.01
                },
            },
        })

        timeScale = chart.timeScale();
        timeScale.applyOptions({
            timeVisible: true,
            secondsVisible: true,
            tickMarkFormatter: msToString,
        })

        resetZoom()

        archiveValues.sensorsArchiveValues.forEach((sensorValue, sensorIdx) => {
            const lineSeries = chart.addLineSeries({
                lastValueVisible: false,
                color: getColor(sensorIdx),
                lineWidth: 2,
            });

            const data = sensorValue.values.map((val, valueIdx) => {
                return {
                    time: archiveValues.time[valueIdx] as Time,
                    value: val
                }
            }).filter(point => point.value != null)
            console.log(data)

            lineSeries.setData(data)
            // lineSeries.setMarkers(data.map(d => {
            //     console.log(d)
            //     return getMarker(d.time, getColor(sensorIdx))
            // }))

            lineSeriesList[sensorIdx] = lineSeries
        })

        chart.subscribeCrosshairMove((event) => {
            crosshairLabelVisible = event.time && event.seriesPrices.size > 0;
            if (!crosshairLabelVisible) return;

            lineSeriesList.forEach((lineSeries, i) => {
                if (!lineSeries.options().visible) {
                    crosshairValues[i] = null
                    return
                }
                const sensor = archiveValues.sensorsArchiveValues[i]
                const prefs = SENSORS_PREFERENCES.get(sensor.sensorType)
                crosshairValues[i] = round(event.seriesPrices.get(lineSeries) as number, prefs.accuracy)
            })

            crosshairCurrentTime = msToString(event.time as number)
            crosshairLabelOffsetX = event.point.x
            crosshairLabelOffsetY = event.point.y
        })


        const onResize = () => {
            chart.resize(chartElement.clientWidth, chartElement.clientHeight, true)
        }
        window.addEventListener('resize', onResize)


        return () => {
            chart.remove()
            window.removeEventListener('resize', onResize)
        }
    })

    const defaultMarker = {
        time: 0,
        position: 'inBar',
        color: "red",
        shape: 'circle',
        size: 0.5
    } as SeriesMarker<Time>


    function getMarker(time: Time, color: string): SeriesMarker<Time> {
        return {...defaultMarker, time, color}
    }

    function getColor(sensorIdx: number): string {
        return chartColors[sensorIdx % chartColors.length]
    }

    function setChartVisible(index: number, isVisible: boolean) {
        lineSeriesList[index].applyOptions({
            visible: isVisible
        })
    }

    // External

    export function zoomIn() {
        const range = timeScale.getVisibleLogicalRange()
        const step = range.to / 10 as Logical;
        range.to -= step;
        range.from += step;
        if (range.to < range.from)
            return;
        timeScale.setVisibleLogicalRange(range)

    }

    export function zoomOut() {
        const range = timeScale.getVisibleLogicalRange()
        const step = range.to / 10 as Logical;
        range.to += step;
        range.from -= step;
        timeScale.setVisibleLogicalRange(range)
    }

    export function resetZoom() {
        const margin = numberOfVisiblePoints / 50;
        timeScale.setVisibleLogicalRange({from: -margin, to: numberOfVisiblePoints + margin})
    }
</script>

<div class="chart-wrapper" class:hidden={!isVisible}>
    <div class="legend">
        {#each archiveValues.sensorsArchiveValues as sensorValues, i}
            <span style:color={getColor(i)}>{SENSORS_PREFERENCES.get(sensorValues.sensorType).name}</span>
            <input class="legend__checkbox" type="checkbox" checked on:change={(e) => {setChartVisible(i, e.target.checked)}}/>
            <br>
        {/each}
    </div>
    <div bind:this={chartElement} class="chart">
        <div class="crosshair-label" hidden={!crosshairLabelVisible} style:top="{crosshairLabelOffsetY}px"
             style:left={crosshairLeftOffset} style:right={crosshairRightOffset}>
            <span>{strings.chartCrosshairValues} </span> <br>
            {#each archiveValues.sensorsArchiveValues as sensorValues, i}
                {#if crosshairValues[i] !== null}
                    <span style:color={getColor(i)}>{SENSORS_PREFERENCES.get(sensorValues.sensorType).name}: {crosshairValues[i]} {sensorValues.unit}</span><br>
                {/if}
            {/each}
            <br>
            <span>{strings.chartCrosshairTime} {crosshairCurrentTime} [{strings.timeFormat}]</span>
        </div>
    </div>
    <div class="chart-label chart-label-x">{labelX}</div>
</div>


<style lang="scss">
  @import "src/styles/colors";
  @import "src/styles/constants";

  .legend {
    position: absolute;
    left: 0;
    top: 0;
    padding: 0.5rem;
    z-index: 100;

    background: white;
    border: 1px solid $gray-500;
    border-radius: $round-sm;
    margin: 2rem;
  }

  .chart-wrapper {
    font-weight: bold;
    grid-template-areas:    "chart chart"
                            "chart-label-x chart-label-x";

    border-radius: $round-md;
  }

  .legend__checkbox {
    margin-left: 8px;
  }


</style>