<script lang="ts">
    import {WebSocketLabRepository} from "./api/repository/WebSocketLabRepository";
    import SavedDataScreen from "./screens/SavedDataScreen.svelte";
    import {Route, Router} from "svelte-navigator";
    import {RoutePath} from "./routes";
    import MainScreen from "./screens/MainScreen.svelte";

    const url = new URL(location.href)
    const serial = url.searchParams.get('serial-number')
    const labRepository = new WebSocketLabRepository(serial)
</script>

<div class="content-wrapper">
    <Router>
        <Route path={RoutePath.Main + '/*'}>
            <MainScreen {labRepository}  />
        </Route>
        <Route path={RoutePath.SavedData + '/:id'} let:params>
            <SavedDataScreen repository={labRepository} id={params.id}/>
        </Route>
    </Router>
</div>


