import type {LabRepository} from "./LabRepository";
import type {FactorySettings} from "../dto/FactorySettings";
import type {LabType} from '../enum/LabType';
import type {byte} from "../types/numbers";
import type {UserSettings} from "../dto/UserSettings";
import type {RawSensorValues} from "../dto/RawSensorValues";
import {Commands} from "../protocol/Commands";
import {ResponseParser} from "../protocol/ResponseParser";
import strings from "../../resources/strings";
import Event from "../utils/Event";
import type { ArchiveValues } from "../dto/ArchiveValues";
import {apiBase} from "../../configuration";


export class WebSocketLabRepository implements LabRepository {

    private socket?: WebSocket
    private rawValues?: RawSensorValues
    private socketIsActive: boolean = false;

    public onError: Event<string> = new Event<string>()

    constructor(private readonly serialNumber?: string) {
    }

    public async start() {
        return new Promise<void>((resolve, reject) => {
            if (this.socket)
                return;

            const uri = new URL(apiBase)
            if (apiBase.protocol == 'https:')
                uri.protocol = "wss:"
            else
                uri.protocol = "ws:"

            uri.pathname += '/subscribe';
            if (this.serialNumber)
                uri.pathname += '/' + this.serialNumber

            console.debug('WS connecting with URI ', uri.toString());
            this.socket = new WebSocket(uri.toString())
            console.debug("socket created")
            this.socket.onopen = () => {
                console.debug("socket connected")
                this.socketIsActive = true
                resolve();
            }
            this.socket.onerror = () => reject(null)
            this.socket.onmessage = (ev) => this.onMessage(ev);
            this.socket.onclose = () => this.onSocketClose()
        })
    }

    public async stop() {
        if (!this.socket)
            return;

        this.socket.close()
        console.debug('socket closed')
        this.socket.onmessage = null;
        this.socket = undefined
        this.socketIsActive = false
    }

    public async getFactorySettings(): Promise<FactorySettings> {
        try {
            const response = await this.sendCommand(Commands.getFactorySettings());
            return ResponseParser.parseFactorySettings(response)
        } catch (e: any) {
            return Promise.reject(e)
        }
    }

    public async getLabType(): Promise<LabType> {
        try {
            const response = await this.sendCommand(Commands.getInfo());
            return ResponseParser.parseLabType(response)

        } catch (e: any) {
            return Promise.reject(e)
        }
    }

    public getLastSensorsValues(): RawSensorValues | undefined {
        if (!this.socketIsActive)
            return {} as RawSensorValues
        return this.rawValues
    }

    public async getUserSettings(labType: LabType): Promise<UserSettings> {
        try {
            const response = await this.sendCommand(Commands.getUserSettings(0, 52));
            return ResponseParser.parseUserSettings(labType, response)

        } catch (e: any) {
            return Promise.reject(e)
        }
    }

    public async setSensorModes(accelerometerMode: byte, conductometerMode: byte): Promise<boolean> {
        try {
            const command = Commands.getSensorValues()
            await this.sendCommand(command)

            return true;
        } catch (e: any) {
            return Promise.reject(e)
        }
    }

    public async sendCommand(request: ArrayBuffer): Promise<ArrayBuffer> {
        try {
            console.debug('send command request: ', request)
            let uri = apiBase + '/send-command';
            if (this.serialNumber)
                uri += '/' + this.serialNumber
            const response = await fetch(uri, {
                body: request,
                method: "POST",
            })
            console.debug('send command response: ', response)
            if (response.ok == false)
                return Promise.reject(response.status)
            const result = await response.arrayBuffer()
            console.debug('send command response buffer: ', result)
            return result
        } catch (e: any) {
            console.debug("result", e)
            return Promise.reject(e)
        }
    }


    private async onMessage(event: MessageEvent) {
        const data = event.data as Blob;
        const arrayBuffer = await data.arrayBuffer();
        // console.debug(arrayBuffer)
        this.rawValues = ResponseParser.ParseRawValues(arrayBuffer);
    }

    private onSocketClose() {
        console.debug("socketClosed. socketIsActive: ", this.socketIsActive)
        this.socket = null;
        if (!this.socketIsActive) return

        this.socketIsActive = false

        this.start()
            .catch(() => {
                this.onError.emit(strings.serverNotResponding)
                console.debug("errorOccurred")
            })
    }

    async getSensorsValuesAsync(): Promise<RawSensorValues> {
        const byteValues = await this.sendCommand(Commands.getSensorValues())
        if (!byteValues)
            return {} as RawSensorValues
        return ResponseParser.ParseRawValues(byteValues)
    }

    async getArchiveValues(id: string): Promise<ArchiveValues | null> {
        const response = await fetch(`${apiBase}/get-archive/${id}`)
        if (response.ok == false)
            return null;

        return await response.json() as ArchiveValues
    }


    async saveArchiveValues(archiveValues: ArchiveValues): Promise<string | null> {
        const response = await fetch(`${apiBase}/upload-archive`, {
            body: JSON.stringify(archiveValues),
            method: 'POST'
        });
        if (!response.ok)
            return null;
        return response.text()
    }

}