import type {float} from "../types/numbers";
import type {LabRepository} from "../repository/LabRepository";
import type {LabType} from "../enum/LabType";
import ReadSettingsException from "../exception/ReadSettingsException";
import type {FactorySettings} from "../dto/FactorySettings";
import type {UserSettings} from "../dto/UserSettings";
import type {RawSensorValues} from "../dto/RawSensorValues";
import type {SensorType} from "../enum/SensorType";
import Sensor from "./Sensor";
import Event, {SubscribeOnlyEvent} from "../utils/Event";
import type {ArchiveValues} from "../dto/ArchiveValues";
import type {SensorArchiveValues} from "../dto/SensorArchiveValues";
import {SensorValuesConverter} from "../protocol/SensorValuesConverter";

const updateInterval = 250;

export default abstract class Lab {

    public abstract availableSensors: ReadonlyArray<SensorType>

    protected abstract labType: LabType;

    private values: Map<SensorType, float | null> = new Map<SensorType, float>()
    private sensors: Sensor[] = [];
    private factorySettings: FactorySettings;
    private userSettings: UserSettings;
    private interval: number;
    private isStopped = true
    private lastRawValues: RawSensorValues | undefined = undefined
    private readonly _onError: Event<string> = new Event<string>()
    public get onError() {return this._onError as SubscribeOnlyEvent<string>}


    protected constructor(private readonly repository: LabRepository) {
        repository.onError.addEventListener(message => {
            this.stop()
            this._onError.emit(message)
        })
    }

    public async init() {
        this.factorySettings = await this.repository.getFactorySettings();
        this.userSettings = await this.repository.getUserSettings(this.labType);
        const sensorValues = await this.repository.getSensorsValuesAsync();

        if (this.factorySettings == null || this.userSettings == null)
            throw new ReadSettingsException();

        this.sensors = this.availableSensors.map(sensorType => new Sensor(this, sensorType))

        this.updateModes(sensorValues)
        return this;
    }

    public getSensors() {
        return this.sensors;
    }

    private update() {
        if (this.isStopped)
            return;

        this.updateValues()
    }

    private updateValues() {
        const rawValues = this.repository.getLastSensorsValues();
        if (rawValues == null || rawValues == this.lastRawValues) {
            this.values.forEach((value, key, map) => {
                map.set(key, null)
            })
            return
        }

        this.lastRawValues = rawValues
        this.availableSensors.map((sensorType) => {
            this.values.set(sensorType,
                SensorValuesConverter.convert(sensorType, rawValues, this.factorySettings, this.userSettings))
        })

        this.updateModes(rawValues)
    }

    protected updateModes(values: RawSensorValues) {}

    public getValue(sensorType: SensorType): float | null {
        return this.values.get(sensorType) ?? null
    }

    setMode(sensorType: SensorType, modeIdx: number): void {

    };

    getMode(sensorType: SensorType): number | undefined {
        return undefined;
    };

    public async start() {
        await this.repository.start()
        this.isStopped = false;
        this.interval = self.setInterval(() => this.update(), 250)
    }

    public async stop() {
        self.clearInterval(this.interval)
        this.isStopped = true;
        await this.repository.stop()
    }

}
