<script lang="ts">
    import strings from "../resources/strings";
    import BackButton from "../components/BackButton.svelte";
    import {DataDisplay} from "../enum/DataDisplay";
    import type {ArchiveValues} from "../api/dto/ArchiveValues";
    import ArchiveChart from "../components/ArchiveChart.svelte";
    import {SensorType} from "../api/enum/SensorType";
    import SensorsTable from "../components/DataTable.svelte";
    import {SENSORS_PREFERENCES} from "../api/repository/sensors";
    import {msToString} from "../api/utils/dateTime";
    import {columnsToRows} from "../api/utils/array";

    // Props

    export let archiveValues: ArchiveValues | undefined

    if (!archiveValues)
        archiveValues = {
            time: [],
            sensorsArchiveValues: []
        }


    // Internal

    let chartEl
    let dataDisplay = DataDisplay.Measures
    let chart

    function toggleMeasureGraphics() {
        dataDisplay = dataDisplay === DataDisplay.Measures ? DataDisplay.Graphics : DataDisplay.Measures
    }


    const tableHeaders = [`${strings.time} [${strings.timeFormat}]`]
    tableHeaders.push(...archiveValues.sensorsArchiveValues
        .map(s => {
            const prefs = SENSORS_PREFERENCES.get(s.sensorType);
            return `${prefs.name} [${s.unit}]`
        }))

    const tableColumns = [archiveValues.time, ...archiveValues.sensorsArchiveValues.map(s => s.values)]
    const rows = columnsToRows(tableColumns)


</script>

<main class="column content-wrapper">
    <header class="row w-full align-center justify-between">
        <div class="row">
            <span class="header__title">
                {strings.workWithSavedData}
            </span>
        </div>

        <img class="header-icon" src="/img/Logging.png" alt="">
    </header>
    <hr class="header-divider"/>
    <div class="work-zone">
        <div class="chart">
            <ArchiveChart bind:this={chart} archiveValues={archiveValues}
                          isVisible={dataDisplay === DataDisplay.Graphics}/>

            <div class="table-wrapper" class:hidden={dataDisplay !== DataDisplay.Measures}>
                <div class="scroll">

                    <table>
                        <thead>
                        <tr>
                            {#each tableHeaders as header}
                                <td>{header}</td>
                            {/each}
                        </tr>
                        </thead>
                        <tbody>
                        {#each rows as row (row[0])}
                            <tr>
                                {#each row as cell, colIdx}
                                    {#if (cell == null)}
                                        <td>{strings.noData}</td>
                                    {:else if (colIdx === 0)}
                                        <td>{msToString(cell)}</td>
                                    {:else }
                                        <td>{cell}</td>
                                    {/if}
                                {/each}
                            </tr>
                        {/each}
                        </tbody>
                    </table>

                </div>
            </div>
        </div>

        <div class="controls control-buttons">
            <button class="measure-graphics" on:click={toggleMeasureGraphics}>
                <img src={dataDisplay === DataDisplay.Measures ? "/img/ico_measure.png" : "/img/ico_graphics.png"}>
            </button>
            <button disabled={dataDisplay === DataDisplay.Measures} on:click={chart.resetZoom}>
                <img src="/img/ico_zoom.png" alt="zoom">
            </button>
            <button disabled={dataDisplay === DataDisplay.Measures} on:click={chart.zoomOut}>
                <img src="/img/ico_zoom_out.png" alt="zoom_out">
            </button>
            <button disabled={dataDisplay === DataDisplay.Measures} on:click={chart.zoomIn}>
                <img src="/img/ico_zoom_in.png" alt="zoom_in">
            </button>
        </div>
    </div>
</main>

