export default class Event<T> {
    private _listeners: ((arg: T) => any)[] = []
    
    public addEventListener(listener: (arg: T) => any) {
        this._listeners.push(listener)
    }
    
    public emit(value: T) {
        for (const listener of this._listeners)
        {
            listener(value)
        }
    } 
}

export type SubscribeOnlyEvent<T> = {
    addEventListener: (listener: (arg: T) => any) => void
}